$col-bg:#fff;
$col-main:teal;
$border-sz:0.1rem;
calendar
*,
*:after,
*:before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

html,
body {
  width: 100%;
  margin: 0;
  // display: flex;
  // justify-content: center;
  // align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 1rem;
  text-align: center;
  background-color: $col-bg;
  color: #111;
}

h1 {
  margin: 2.5rem 0 1.5rem 0;
  font-weight: normal;
  letter-spacing: 3px;
}

h2 {
  font-weight: normal;
  letter-spacing: 3px;
}

a {
  text-decoration: none;
  color: #111;
  transition: all 0.3s ease;
  cursor: pointer;

  &:hover {
    color: $col-main;
  }
}

button {
  font: inherit;
  font-size: 1.4rem;
  font-weight: normal;
  background: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;

  &.btn-main {
    color: $col-bg;
    background: $col-main;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }
}

input {
  font: inherit;
  font-weight: normal;
  border: $border-sz solid rgba(0, 0, 0, 0.1);
  outline: 0;

  &::placeholder {
    color: rgba(0, 0, 0, 0.4);
  }

  &.input-main {
    border: $border-sz solid $col-main;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    letter-spacing: 2px;
  }
}

.btn-main:hover {
  color: $col-main;
  background: white;
  border: 1px solid teal;
   transition: 0.3s;
}


input,
.btn-main {
  // height: 2.4rem;
  padding: 0.8rem 1rem;
  line-height: 1;
}

.input-group {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;

  input {
    width: 80%;
  }

  button {
    width: 20%;
  }
}

#app {
  width: 21rem;
}

.search {
  width: 100%;
  margin: 1.0rem 0 1.0rem 0;

  input {
    width: 100%;
  }
}

.month {
  align-items: center;
  margin: 0;
  font-size: 1.1rem;
  min-width:350px;
  max-width:350px;

  .month-active {
    width: 12rem;
  }

  .month-selector {
    a {
      display: inline-block;
      width: 2rem;
      height: 2rem;
      line-height: 2rem;
    }
  }

  .nextYear,
  .prevYear {
    font-size: 1.6rem;
    font-weight: normal;
  }

  .month-active,
  .month-selector {
    display: inline-block;
    padding: 0.5rem;
    letter-spacing: 2px;
  }
}

.weekdays {
  font-weight: 600;
}

.weekdays,
.days {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  user-select: none;
  min-width:350px;
  max-width:350px;
}

.weekdays,
.days {
  .day {
    width: 3rem;
    height: 3rem;
    line-height: 3rem;
    letter-spacing: 1px;
  }
}

.days {
  border-top: $border-sz solid rgba(0, 128, 128, 0.4);
  border-bottom: $border-sz solid rgba(0, 128, 128, 0.3);

  .day {
    position: relative;
    cursor: pointer;
    color:#111;
   

    &:not(.day-empty):not(.day-active):not(.has-matches):hover {
      color: $col-main;
    }

    &.day-active {
      color: black;
      background-image: url('https://i.pinimg.com/originals/fc/f2/1a/fcf21adf18121b1edcad4a1c82c66025.jpg');


   
      background-repeat: no-repeat;
      background-position: center;
      background-size: 3rem;
      width: 3rem;
      height: 3rem;
      border-radius: 10px;

      &.has-events::before {
        background: black;
      }
    }
    .dayLightImage{
      background-image: url('https://i.pinimg.com/originals/fc/f2/1a/fcf21adf18121b1edcad4a1c82c66025.jpg');

    }
    .dayDarkImage {
      background-image: url('../../image/calendar.png');
    }

    &.day-empty {
      cursor: initial;
    }

    &.has-events {
      &::before {
        content: "";
        position: absolute;
        display: block;
        top: 0.40rem;
        left: 0.40rem;
        width: 0.35rem;
        height: 0.35rem;
        border-radius: 50%;
        background: $col-main;
      }
    }

    &.has-matches:not(.day-active) {
      background: rgba(255, 0, 0, 0.1);
      color: $col-main;
    }
  }

}
.section-container{
  display: flex;
  justify-content: space-between;
}

.events {
  // border-bottom: $border-sz solid rgba(0, 128, 128, 0.3);
  margin-top: 30px;

  ul {
    min-width: 400px;

    li {
      // list-style-type: circle;
      // word-break: break-word;
      text-align: left;
     
      font-size: 1.2rem;
      font-weight: normal;
      letter-spacing: 1px;
      list-style: none;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-radius: 10px;
      padding: 0 10px;
      margin-bottom: 10px;
      min-width: 80%;
   

      a {
        padding: 0 0.5rem;
        line-height: 2rem;
        font-weight: bold;
        transition: 0.3s all ease;
        opacity: 0;

        &:hover {
          opacity: 1;
        }
      }
    }
  }

  p {
    margin: 0 0 2rem 0;
  }
}

.container-event-add{
  display: flex;
  flex-direction: column;
  background-color: rgba(120, 147, 132, 0.522);
  border-radius: 0px 20px 0px 20px;
  padding: 30px;
  
}
.calendar-container{
    background-color: rgba(120, 147, 132, 0.522);
    border-radius: 20px 0px 20px 0px;
}

.event-add {
  // margin: 2rem 0 4rem 0;
}
.correct-todo{
  display: flex;
}
.lightText{
  color:#111;
}
.darkText {
  color:#fff;
}
.reset {
  font-size: 15px;
  font-weight: normal;
  letter-spacing: 2px;
  width: 75%;
  margin-bottom: 10px;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  background-color: #000000aa;
  color: white;
  padding: 10px;
  border-radius: 4px;
  outline: none;
}

todayButton:hover {
  cursor: pointer;
}
.btn-add-event{
    display: flex;
    align-items: center;
    cursor:pointer
}
.btn-back-wrap{
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 80%;
}

@media only screen and (max-width: 32.0rem) {
  #app {
    width: 95%;
  }

  .calendar {
    max-width: 100%;



    .days,
    .weekdays {
      width: 100%;
    }

    .reset {
      font-size: 4vw;
    }

    .day {
      font-size: 4.5vw;
      width: 12.5vw;
      height: 12.5vw;
      line-height: 12.5vw;

      &.has-events::before {
        top: 1.5vw;
        left: 1.5vw;
        width: 1.5vw;
        height: 1.5vw;
      }

      &.day-active {
        background-size: 12.5vw;
        width: 12.5vw;
        height: 12.5vw;
      }
    }
  }
}